.Card {
    padding: 0px 50px;
    margin-bottom: 15px;
    position: relative;
    opacity: 75%;
    & span {
        display: none;
    }
    &:hover {
        opacity: 100%;
        & span {
            background-color: #d2c8c8;
            display: block;
            position: absolute;
            left: 220px;
            top: 0px;
            width: 300px;
            height: 350px;
            padding: 10px;
            opacity: 95%;
            z-index: 3;
            overflow-y: scroll;
        }
    }
}

.Text {
    &__TitleYear {
        display: flex;
        flex-direction: row;
    }
}

.Title {
    font-weight: bold;
    &:hover {
        text-decoration: underline;
        cursor: pointer;
    }
}

.Author {
    &:hover {
        text-decoration: underline;
        cursor: pointer;
    }
}

.Year {
    font-style: italic;
    margin-left: 10px;
}

.Img {
    height: 240px;
    width: 160px;
    cursor: pointer;
}

.Description {
    color: #464545;
}

.Button {
    border-radius: 8px;
    border: 1px;
    line-height: 1.5;
    cursor: pointer;
}

.More {
    & span {
        display: none;
    }
    display: none;
}
